body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background: black;
}
.app {
  text-align: center;
  color: white;
}

.score{
  color: white;
  position: absolute;
  left: 80px;
  /* background: blue; */
  top:0px;
}
.canvas{
  position: relative;
  margin-top: 10px;
  text-align: center;
  /* background: green; */
}
.paused{
  position: absolute;
  left:0px;
  right:0px;
  margin: auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: white;
  -webkit-text-stroke: 2px black;
}
.start{
  color: white;
  position: absolute;
  top: 0px;
  right: 80px;
  /* background: red; */
}
.gameStart button{
  font-size: 16px;
  height: 50px;
  width: 100px;
}
.points{
  margin-top: -30px;
}
@media only screen and (max-width: 1024px){

  .score{
    position: absolute;
    top: 0px;
    left: 0px;
  }
  .start{
    position: absolute;
    right:0px;
    margin: auto;
  }
  .gameStart button{
    -webkit-appearance: none;
    -webkit-border-radius: 0px;
    height:50px;
    width:60px;
    background: white;
    padding: 0px;
  }
  .points{
    margin-top:0px;
  }
  .points h1{
    font-size: 20px;
  }
  .points h2{
    font-size: 16px;
  }
  .controlDiv{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top: -10px;
  }
  .left button, .right button, .middle button{
    font-size: 30px;
    font-weight: bold;
    height: 75px;
    width: 75px;
    text-align: center;
    -webkit-appearance: none;
    -webkit-border-radius: 0px;
    background: white;
    padding: 0px;
  }
  .middle button{
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .middle {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 10px;
  }
}

